* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

nav {
  display: flex;
  align-items: center;
  padding: 1rem;
  background-color: #2a6aa5;
}

nav * {
  color: white;
  margin-right: 1rem;
}

main {
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

h1 {
  font-size: 3.6rem;
}

input {
  width: 13rem;
  padding: 0.5rem;
  color: black;
  cursor: pointer;
  transition: 0.5s;
  font-size: 0.8rem;
  margin-top: 2rem;
  letter-spacing: 2px;
}

input:nth-child(3) {
  background-color: #2a6aa5;
  border: 1px solid transparent;
}

input:nth-child(3):hover {
  color: #2a6aa5;
  border: 1px solid #2a6aa5;
  background-color: white;
}

form {
  display: flex;
  flex-direction: column;
}

.alertPara {
  color: red;
  margin-top: 1rem;
  font-weight: bold;
}
